<template>
    <div class="favorite">
        <p class="title" v-text ="!openParity? '我的收藏':'我的比價'"></p>
        <p class="sub-title">MEMBER PORFOLE</p>
        <el-select v-show="!openParity" v-model="value" placeholder="篩選排序" class="favorite-select">
            <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
        <div v-if="!openParity" class="favorite-content">
            <div class="scroll-x">
                <el-table
                    :data="tableData"
                    :key="Math.random()"
                    style="width: 100%">
                    <el-table-column
                        label="項目"
                        min-width="400"
                        >
                        <div class="search-result-card">
                            <div class="card-img">
                                <img src="~@/assets/img/cartype/photo-1.png" alt="" srcset="">
                            </div>
                            <div class="card-info">
                                <p class="card-tag"><i class="el-icon-price-tag"/>二手車商</p>
                                <p class="card-title">Aston Martin Vantage</p>
                                <p class="card-txt">In a pickup market gone fancy, the Silverado sticks to its basic-truck recipe. The steering is accurate, and the Silverado handles more like a big car than a big truck.</p>
                                <div class="card-money">
                                    <img src="~@/assets/img/cartype/money-icon-w.png" alt="" srcset=""> 
                                    <p>149.8 萬</p>
                                </div>
                                <div class="card-collect">
                                    <div :class="{'card-collect-active': collect }" @click="collectCar"><i :class="{'el-icon-star-on': collect }" class="el-icon-star-off"/>325 人蒐藏</div>
                                    <span />
                                    <div><i class="el-icon-view"/>1368 人瀏覽</div>
                                </div>
                            </div>
                        </div>
                    </el-table-column>
                    <el-table-column
                        prop="name"
                        label="賣家"
                        width="180"
                        align="center">
                        <div class="head">
                            <HeadIcon :width='60' />
                            <br>王曉明</div>
                        
                    </el-table-column>
                    <el-table-column
                        prop="address"
                        label="加入比較"
                        fixed="right"
                    >
                        <el-checkbox v-model="tableData.checked">比價</el-checkbox>
                    </el-table-column>    
                </el-table>
            </div>
            <el-pagination
                layout="prev, pager, next"
                :total="50">
            </el-pagination>
            <div class="favorite-float">
                <div class="car-box">
                    <div class="float-car" v-for ="item in 3 " :key="item"  >
                        <i class="el-icon-close" />
                        <img src="~@/assets/img/cartype/photo-1.png" alt="" srcset="" class="">
                        <p>Aston Martin Vantage</p>
                    </div>
                </div>
                <p class="btn" @click="setParityCar()"><span>開始比價</span><br>(3)</p>
            </div>
        </div>
        <parityCar v-else @backList="setParityCar" />
    </div>
</template>

<script>
import HeadIcon from '../../../layout/headIcon.vue'
import parityCar from './parityCar.vue'
export default {
    name:'favorite',
    components: {
        parityCar,
        HeadIcon,
    },
    data() {
        return {
            collect: false,
            options: [{
                value: '选项1',
                label: '價錢低→高'
                }, {
                value: '选项2',
                label: '價錢高→低',
                },
            ],
            value: '',
            tableData: [
                {
                name: '王曉明',
                head: '',
                checked: true,
                }, 
                {
                    name: '王曉明',
                    head: '',
                    checked: false,
                },
                {
                    name: '王曉明',
                    head: '',
                    checked: false,
                }
            ],
            openParity: false,
        }
    },
    methods: { 
        setParityCar() {
            this.openParity = !this.openParity
        },
        collectCar() {
            this.collect = !this.collect
        },
    }
}
</script>

<style lang='scss' scoped>
.favorite {
    padding: 20px 0;
    margin: 0 auto;
    width: 100%;
    .title {
        color: #333333;
        font-size: 40px;
        font-weight: bold;
        text-align: center;
    }
    .sub-title {
        color: #ef6d38;
        font-size: 25px;
        text-align: center;
    }
    .favorite-select {
        margin: 50px 0 20px 0;
        position: absolute;
        right: 50px;
        z-index: 1;
    }
    .favorite-content {
        padding: 120px 50px;
        position: relative;
        .head {
            text-align: center;
            i {
                font-size: 50px;
                background: #fff;
                border: 2px #999 solid;
                border-radius: 40px;
                padding: 5px;
                color: #999;
                margin-bottom: 10px;
            }
        }
        .search-result-card{
            display: flex;
            padding: 30px 0;
            &:nth-last-child(2) {
                border-bottom:none
            }
            .card-img {
                position: relative;
                cursor: pointer;
                img {
                    max-width: 100%;
                    width: 300px;
                }
            }
            .card-info {
                margin-left: 20px;
                
                .card-tag {
                    color: #fff;
                    background: #3cb07b;
                    line-height: 40px;
                    border-radius: 50px;
                    text-align: center;
                    padding: 0 30px 0 45px;
                    position: relative;
                    font-weight: bold;
                    max-width: 90px;
                    i{
                        background: #148451;
                        border-radius: 50px;
                        line-height: 30px;
                        width: 30px;
                        transform: rotate(45deg);
                        position: absolute;
                        top: 5px;
                        left: 5px;
                    }
                }
                .card-title {
                    font-size: 22px;
                    font-weight: bold;
                    margin: 30px 0 15px 0;
                    text-decoration: underline;
                }
                .card-txt {
                    max-width: 400px;
                    line-height: 25px;
                }
                .card-money {
                    background: #ef6d38;
                    line-height: 50px;
                    color: #fff;
                    font-size: 24px;
                    display: flex;
                    margin: 30px 0;
                    max-width: 225px;
                    img {
                        background: #ca582a;
                        padding:10px;
                    }
                    p {
                        padding-left: 10px;
                    }
                }
                .card-collect {
                    display: flex;
                    div {
                        margin: 10px;
                        padding: 0 10px;
                        transition: all .3s;
                        cursor: pointer;
                        i {
                            margin-right: 10px;
                        }
                    }
                    .card-collect-active {
                        color: #ef6d38;
                    }
                    span {
                        width: 1px;
                        display: block;
                        background: #dddddd;
                    }
                }
            }
            @media screen and (max-width: 768px){  
                align-items: center;
                flex-direction: column;
                .card-info {
                    margin: 20px 0;
                    .card-title {
                        text-align: center;
                    }
                    .card-money {
                        margin: 30px auto;
                    }
                    .card-collect {
                        margin: auto;
                        right: auto;
                    }
                }
                .card-tag {
                        margin: auto;
                    }
            }
        }
        .favorite-float {
            position: fixed;
            bottom: 75%;
            right: 0;
            z-index: 10;    
            .btn {
                background: #333333;
                color: #fff;
                padding: 10px;
                border-radius: 5px 0 0 5px;
                cursor: pointer;
                right: 0;
                span {
                    writing-mode: vertical-rl;
                }
            }
            .car-box {
                position: absolute;
                background: #efefef;
                padding: 20px 50px 0px 20px;
                z-index: -1;
                top: -40px;
                right: 0;
                box-sizing: border-box;
                display: none;
                flex-wrap: wrap;
                max-width: 520px;
                width: max-content;
            }
            .float-car {
                margin-right: 20px;
                background: #fff;
                position: relative;
                margin-bottom: 20px;
                .el-icon-close {
                    position: absolute;
                    z-index: 1;
                    top: 5px;
                    right: 5px;
                    color: #fff;
                    background: #333333;
                }
                img {
                    width: 130px;
                }
                p {
                    font-size: 10px;
                    text-align: center;
                    line-height: 20px;
                }
            }
            &:hover {
                .car-box {
                    display: flex;
                    opacity: 1;
                }
            }
            @media screen and (max-width: 768px){
                bottom: 60px;
                .car-box {
                    display: none;
                    // max-width: 100vw;
                    // overflow: auto;
                    // display: flex;
                    // flex-wrap: inherit;
                    // flex-direction: row-reverse;
                }
            } 
        }
    }

    .scroll-x{
        max-width: calc(100vw - 575px);
        overflow-x: auto;
    }

    @media screen and (max-width: 768px){ 
        
        padding: 50px 0; 
        .favorite-content {
            padding: 50px 20px;
            overflow: auto;

        }
        .favorite-select {
            position: static;
            padding: 0 20px;
            box-sizing: border-box;
        }

        .scroll-x{
            overflow: visible;
            max-width: none;
        }
        
    }



    
}

</style>


