<template>
  <div class="parityCar">
    <div class="scroll-x">
      <el-table
        :data="tableHead"
        border
        style="width: 100%"
        :show-header="false"
      >
        <el-table-column
          prop="label"
          align="center"
          min-width="200"
          class-name="item-name"
        >
        </el-table-column>
        <template v-for="(col, i) in tableData">
          <el-table-column :key="`item_${i}`" align="center" min-width="480">
            <template slot-scope="scope">
              <div v-if="scope.row.prop == 'head'" class="search-result-card">
                <div class="card-img">
                  <img
                    src="~@/assets/img/cartype/photo-1.png"
                    alt=""
                    srcset=""
                  />
                </div>
                <div class="card-info">
                  <p class="card-tag">
                    <i class="el-icon-price-tag" />二手車商
                  </p>
                  <p class="card-title">Aston Martin Vantage</p>
                  <p class="card-txt">
                    In a pickup market gone fancy, the Silverado sticks to its
                    basic-truck recipe. The steering is accurate, and the
                    Silverado handles more like a big car than a big truck.
                  </p>
                  <div class="card-money">
                    <img
                      src="~@/assets/img/cartype/money-icon-w.png"
                      alt=""
                      srcset=""
                    />
                    <p>149.8 萬</p>
                  </div>
                </div>
              </div>
              <span v-else>
                {{ col[scope.row.prop] }}
              </span>
            </template>
          </el-table-column>
        </template>
      </el-table>
    </div>
    <button class="car-btn" @click="$emit('backList')">返回列表</button>
  </div>
</template>

<script>
export default {
  name: "parityCar",
  data() {
    return {
      tableHead: [
        {
          prop: "head",
        },
        {
          prop: "mileage",
          label: "里程",
        },
        {
          prop: "date",
          label: "領牌時間",
        },
        {
          prop: "color",
          label: "顏色",
        },
        {
          prop: "drive",
          label: "車門乘客數",
        },
        {
          prop: "fuel",
          label: "燃料",
        },
        {
          prop: "drive",
          label: "驅動方式",
        },
      ],
      tableData: [
        {
          img: "",
          mileage: "19,999",
          date: "2019/10/1",
          color: "白色",
          drive: "5門5座",
          fuel: "汽油",
          drive: "2WD",
        },
        {
          img: "",
          mileage: "29,999",
          date: "2019/10/2",
          color: "白色",
          drive: "5門5座",
          fuel: "汽油",
          drive: "2WD",
        },
        {
          img: "",
          mileage: "39,999",
          date: "2019/10/3",
          color: "白色",
          drive: "5門5座",
          fuel: "汽油",
          drive: "2WD",
        },
        {
          img: "",
          mileage: "49,999",
          date: "2019/10/4",
          color: "白色",
          drive: "5門5座",
          fuel: "汽油",
          drive: "2WD",
        },
      ],
    };
  },
};
</script>

<style lang='scss' scoped>
.parityCar {
  width: calc(100vw - 495px);
  margin: 100px auto;
  .scroll-x {
    max-width: calc(100vw - 575px);
    overflow-x: auto;
  }
  .search-result-card {
    padding: 30px 0;
    &:nth-last-child(2) {
      border-bottom: none;
    }
    .card-img {
      position: relative;
      cursor: pointer;
      img {
        width: 200px;
        height: 220px;
      }
    }
    .card-info {
      .card-tag {
        color: #fff;
        background: #3cb07b;
        line-height: 40px;
        border-radius: 50px;
        text-align: center;
        padding: 0 30px 0 45px;
        position: relative;
        font-weight: bold;
        max-width: 90px;
        margin: 20px auto 0;
        i {
          background: #148451;
          border-radius: 50px;
          line-height: 30px;
          width: 30px;
          transform: rotate(45deg);
          position: absolute;
          top: 5px;
          left: 5px;
        }
      }
      .card-title {
        font-size: 22px;
        font-weight: bold;
        margin: 30px 0 15px 0;
        text-decoration: underline;
      }
      .card-txt {
        max-width: 400px;
        line-height: 25px;
      }
      .card-money {
        background: #ef6d38;
        line-height: 50px;
        color: #fff;
        font-size: 16px;
        display: flex;
        margin: 20px auto 0;
        max-width: 200px;
        img {
          background: #ca582a;
          padding: 10px;
        }
        p {
          padding: 0 10px;
          width: 100%;
          text-align: center;
        }
      }
      .card-collect {
        display: flex;
        div {
          margin: 10px;
          padding: 0 10px;
          transition: all 0.3s;
          cursor: pointer;
          i {
            margin-right: 10px;
          }
          &:hover {
            color: #ef6d38;
          }
        }
        .card-collect-active {
          color: #ef6d38;
        }
        span {
          width: 1px;
          display: block;
          background: #dddddd;
        }
      }
    }
    @media screen and (max-width: 768px) {
      align-items: center;
      flex-direction: column;
      .card-info {
        margin: 20px 0;
        .card-title {
          text-align: center;
        }
        .card-money {
          margin: 30px auto;
        }
        .card-collect {
          margin: auto;
          right: auto;
        }
      }
      .card-tag {
        margin: auto;
      }
    }
  }
  @media screen and (max-width: 768px) {
    width: 90%;
    margin: 0 auto;
    .scroll-x {
      max-width: none;
    }
  }
  >>> .el-table {
    .el-table__row > td:first-child {
      font-weight: bold;
    }
    .cell {
      font-size: 15px;
      padding: 10px 5px;
    }
  }
}
</style>


